.@{prefixCls}@{pickerAnimationPrefixCls}{
  &-fade-enter-active,&-fade-appear{
    opacity: 0;
    animation: mdniFadeIn .3s cubic-bezier(0.55, 0, 0.55, 0.2);
  }
  &-fade-leave-active{
    animation: mdniFadeOut .3s cubic-bezier(0.55, 0, 0.55, 0.2);
  }
}

@keyframes mdniFadeIn {
    0% {
    opacity: 0;
    }
    100% {
    opacity: 1;
    }
}
@keyframes mdniFadeOut {
    0% {
    opacity: 1;
    }
    100% {
    opacity: 0;
    }
}
