.@{prefixCls}@{pickerAnimationPrefixCls}{
  &-slide-enter-active,&-slide-appear{
    transform: translate(0, 100%);
    animation: mdniSlideIn .3s cubic-bezier(0.55, 0, 0.55, 0.2);
  }
  &-slide-leave-active{
    animation: mdniSlideOut .3s cubic-bezier(0.55, 0, 0.55, 0.2);
  }
}

@keyframes mdniSlideIn {
    0% {
        transform: translate(0, 100%);
    }
    100% {
        transform: translate(0, 0);
    }
}

@keyframes mdniSlideOut {
    0% {
        transform: translate(0, 0);
    }
    100% {
        transform: translate(0, 100%);
    }
}
